.project-container {
    display: flex;
    text-align: justify;
    justify-content: justify;
    height: 10%;
    padding: 2% 12% 12%;
}
.project-parent {
    display: flex;
    align-items: justify;
    color: aliceblue;
}

.MuiPaper-root {
    font-size: 24px;
    font-family: "Poppins Semibold";
    padding: 0;
    margin: 5px
}

.primary-headline h1{
    color: cornsilk;
    text-align: center;
}

.primary-headline h1 .underline {
    position: relative;
    text-decoration: none;
    color: cornsilk;
}

.primary-headline h1 .underline::after {
    position: absolute;
    content: '';
    height: 20px;
    bottom: 10px;
    left: 0;
    right: 0;
    width: 100%;
    background: #088395;
    border-radius: 5px;
    z-index: -1;
}


.project-text {
    font-size: 19px;
    margin: 5px 0 0 0;
    font-family: "Poppins Light";
    text-align: start;
}

.project-options {
    margin-top: 20px;
    display: flex;
    justify-content: flex-start;
}


.webApp-card {
    max-width: 60%; 
}

.landingPage-card {
    max-width: 40%; 
}





@media only screen and (max-width: 767px) {

    .project-picture {
        margin: 0;
        height: 200px;
        width: 200px;
    }

    .project-options {
        display: flex;
        justify-content: space-between;
    }

    
    .project-options .highlighted-btn:hover {
        margin: 0 0 0 10px;
        background-color: cornsilk;
        color: #111
    }

    .project-details {
        margin: 25px 0 0;
    }

    .project-container {
        display: flex;
        text-align: justify;
        justify-content: justify;
        height: 10%;
        padding: 2%;
    }

    .webApp-card, .landingPage-card {
        max-width: 100%; 
    }
}