.profile-container {
    display: flex;
    align-items: center;
    text-align: center;
    justify-content: center;
    height: 10%;
    padding: 2% 12% 12%;
}
.profile-parent {
    display: flex;
    align-items: center;
    color: aliceblue;
}

.profile-details-name {
    font-size: 24px;
    font-family: "Poppins Semibold";
}

.profile-details-role {
    display: flex;
    flex-direction: "column";
    margin: 25px;
}

.profile-details-role h1{
    font-size: 40px;
    font-family: cursive;
}

.primary-text {
    color: (--white);
}

.highlighted-text {
    color: #00FFCA;
}

.profile-role-tagline {
    font-size: 19px;
    margin: 5px 0 0 0;
    font-family: "Poppins Light";
}

.profile-options {
    margin-top: 20px;
    display: flex;
    justify-content: center;
}

.profile-options .highlighted-btn {
    margin: 0 0 0 28px;
}

.profile-options .highlighted-btn:hover {
    margin: 0 0 0 28px;
    background-color: aliceblue;
    color: #0A4D68;
}

.profile-picture {
    display: flex;
    align-items: center;
    justify-content: center;
    /* border-radius: 50%; */
    box-shadow: 0 0 2px 0 #00FFCA;
    height: 300px;
    width: 300px;
    margin: 0 0 0 10%;
    border-radius: 50% 25% 25% 25%;
}

.profile-picture-background {
    height: 100%;
    width: 100%;
    background-image: url('../../assets/Home/html-concept-bg.webp');
    background-size: cover;
    border-radius: 50% 25% 25% 25%;
}


.social .social-icon i {
    color: white;
    padding: 10px;
    padding-left: 0px;
}

.social .social-icon i:hover {
    transform: scale(1.5);
}

@media only screen and (max-width: 767px) {

    .profile-parent {
        flex-direction: column-reverse;
        margin-right: 5%;
        margin-left: 5%;
    }

    .profile-picture {
        margin: 0 50px;
        height: 200px;
        width: 200px;
    }

    .profile-options {
        flex-direction: column;
        display: flex;
        align-items: center;
    }

    .profile-options .highlighted-btn {
        margin: 10px 0 0 0;
    }
    
    .profile-options .highlighted-btn:hover {
        margin: 0 0 0 10px;
        background-color: cornsilk;
        color: #111
    }

    .primary-btn .highlighted-btn{
        margin: 5px 0 5px 0;
    }

    .profile-details {
        margin: 25px 0 0;
    }
}