.contact-container {
    display: flex;
    text-align: justify;
    justify-content: justify;
    height: 10%;
    padding: 2% 10% 10%;
}
.contact-parent {
    display: flex;
    align-items: justify;
    color: aliceblue;
}

.contact-headline h1{
    color: cornsilk;
    text-align: start;
}

.contact-subheading {
    font-size: 16px;
    margin: 5px 0 0 0;
    font-family: "Poppins Light";
    text-align: justify;
    color: aliceblue;
}

.contact-headline h1 .underline {
    position: relative;
    text-decoration: none;
}

.contact-headline h1 .underline::after {
    position: absolute;
    content: '';
    height: 20px;
    bottom: 10px;
    left: 0;
    right: 0;
    width: 100%;
    background: #088395;
    border-radius: 5px;
    z-index: -1;
}


.contact-btn {
    background-color: #088395;
    color: cornsilk;
    text-decoration: none;
    border-radius: 60px;
    height: 50px;
    display: inline-flex;
    align-items: center;
    overflow:hidden;
    width: auto;
    max-width: 50px;
    -webkit-transition: max-width 0.5s;
    transition: max-width 0.5s;
  }
  
  .contact-btn:hover {
    max-width: 300px;
  }
  
  .icon {
    margin: 0 15px 0 2px;
    padding: 0px 8px;
    display: flex;
    align-items: center;
    color: cornsilk;
  }
  
  .text {
    white-space: nowrap;
    padding-right: 15px;
  }


  @media only screen and (max-width: 767px) {
    .contact-container {
        height: 10%;
        padding: 2%;
    }
}