.about-container {
    height: 10%;
    padding: 2% 12% 12%;
}

.about-intro, .about-skills {
    font-size: 16px;
    margin: 5px 0 0 0;
    font-family: "Poppins Light";
    text-align: justify;
    color: aliceblue;
}

.skills-details {
    font-size: 24px;
    font-family: "Poppins Semibold";
}

.about-headline h1{
    color: cornsilk;
    text-align: start;
}

.about-headline h1 .underline {
    position: relative;
    text-decoration: none;
}

.about-headline h1 .underline::after {
    position: absolute;
    content: '';
    height: 20px;
    bottom: 10px;
    left: 0;
    right: 0;
    width: 100%;
    background: #088395;
    border-radius: 5px;
    z-index: -1;
}

.about-btn {
    display: flex;
    align-items: center;
    text-align: center;
    margin-top: 20px;
}

.about-options .highlighted-btn {
    margin: 0;
}


.about-picture {
    display: flex;
    align-items: center;
    justify-content: center;
    /* border-radius: 50%; */
    box-shadow: 2px 2px 0 0 #088395;
    height: 180px;
    width: 180px;
    margin: auto;
    border-radius: 50%;
}

.about-picture-background {
    height: 97%;
    width: 97%;
    background-image: url('../../assets/Home/profilephoto.jpg');
    background-size: contain;
    border-radius: 50%;
}

.about-picture-background:hover {
    transform: scale(1.03);
    transition: 1s ease
}

.skill {
    background-color: var(--sea-green-shadow);
    padding: 5px;
    margin: 5px;
    border-radius: 5px;
    font-size: 14px;
    display: inline;
}


.about-btn .highlighted-btn:hover {
    background-color: aliceblue;
    color: #0A4D68;
}


@media only screen and (max-width: 767px) {
    .about-container {
        height: 10%;
        padding: 0;
    }

    .about-picture {
        margin: auto;
        height: 150px;
        width: 150px;
    }

    .about-options {
        display: flex;
        justify-content: space-between;
    }

    
    .about-options .highlighted-btn:hover {
        background-color: cornsilk;
        color: #111
    }

    .about-details {
        margin: 25px 0 0;
    }
}