.footer-container {
    display: flex;
    align-items: center;
    text-align: center;
    justify-content: center;
    width: 100%;
    padding: 20px 0 20px 0;
    color: aliceblue;
    font-size: 12px;
    border-top: 2px solid;
    border-color: rgba(0, 0, 0, 0.12);
}

.footer-icons a svg {
    color: aliceblue;
    margin: 0 10px;
}

.footer-icons a svg:hover {
    transform: scale(1.5);
}
